import { useState, useEffect } from "react";
import {
  Heading,
  Layout,
  Page,
  DataTable,
  Stack,
  TextStyle,
  IndexTable,
  Card,
  useIndexResourceState,
  Button,
  Link,
} from "@shopify/polaris";
import axios from "axios";
import User from "./User";
import { toast } from "react-toastify";
import { logout } from "./auth";

const Manager = () => {
  const [user, setUser] = useState("");
  const [sms, setsms] = useState([]);
  const Fetchsms = () => {
    if (user !== "" && user !== "Select User") {
      axios
        .get(`/sms/${user}`)
        .then((res) => {
          setsms(res.data.sms);
        })
        .catch((err) => {
          console.error(err);
        });
    }
  };

  useEffect(() => {
    Fetchsms();
  }, [user]);
  const DeleteSMS = () => {
    axios
      .get(`/sms/delete/${user}`)
      .then((res) => {
        toast.success(res.data.message);
      })
      .catch((err) => {
        toast.error("Error Deleting All SMS");
      });
    Fetchsms();
  };
  return (
    <Page fullWidth title="SMS Manager">
      <Stack vertical>
        <Stack.Item>
          <div
            onClick={() => (window.location.href = "/device")}
            style={{ cursor: "pointer" }}
          >
            <TextStyle variation="positive">
              <Heading>Clients</Heading>
            </TextStyle>
          </div>
          <div onClick={() => logout()} style={{ cursor: "pointer" }}>
            <TextStyle variation="negative">
              <Heading>Logout</Heading>
            </TextStyle>
          </div>
        </Stack.Item>
        <Stack distribution="equalSpacing">
          <User selected={user} setSelected={setUser} />
          <Button destructive onClick={() => DeleteSMS()}>
            Delete clients SMS
          </Button>
        </Stack>
      </Stack>
      <br></br>

      <Card>
        <DataTable
          columnContentTypes={["text", "text", "text", "text"]}
          headings={["From", "SMS", "Device_id", "Date"]}
          rows={sms}
        />
      </Card>
    </Page>
  );
};
export default Manager;
