import { useState, useCallback } from "react";
import enTranslations from "@shopify/polaris/locales/en.json";
import { AppProvider, Page, Card, TextField, Button } from "@shopify/polaris";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useRoutes,
  Link as ReactRouterLink,
  Navigate,
} from "react-router-dom";
import { isLogin } from "./auth";
import Sms_manager from "./sms_manager";
import Login from "./Login";
import Client from "./client"

function App() {
  const AppRoute = () => {
    let routes = useRoutes([
      { path: "/", element: isLogin() ? <Navigate to="/home" /> : <Navigate to="/login" /> },
      { path: "/login", element: isLogin() ? <Navigate to="/home" /> : <Login /> },
      {
        path: "/home",
        element: isLogin() ? <Sms_manager /> : <Navigate to="/login" />,
      },
      {
        path: "/device",
        element: isLogin() ? <Client /> : <Navigate to="/login" />,
      },
      // ...
    ]);
    return routes;
  };
  return (
    <AppProvider i18n={enTranslations} linkComponent={Link}>
      <Router>
        <AppRoute />
      </Router>
    </AppProvider>
  );
}
function Link({ children, url = "", ...rest }) {
  // Use an regular a tag for external and download links
  if (isOutboundLink(url) || rest.download) {
    return (
      <a href={url} {...rest}>
        {children}
      </a>
    );
  }

  return (
    <ReactRouterLink to={url} {...rest}>
      {children}
    </ReactRouterLink>
  );
}

function isOutboundLink(url) {
  return /^(?:[a-z][a-z\d+.-]*:|\/\/)/.test(url);
}

export default App;
