import { useState, useEffect } from "react";
import {
  Heading,
  Layout,
  Page,
  DataTable,
  Stack,
  TextStyle,
  IndexTable,
  Card,
  useIndexResourceState,
  Button,
} from "@shopify/polaris";
import axios from "axios";
import User from "./User";
import { toast } from "react-toastify";
import { logout } from "./auth";
import { Link } from "react-router-dom";

const Manager = () => {
  const [user, setUser] = useState([]);
  const FetchClient = () => {
    if (user !== "" && user !== "Select User") {
      axios
        .get(`/client/`)
        .then((res) => {
            console.log(res.data)
            var users = []
            res.data.clients.map((item) => {
                users.push([item.client_id, item.android_version,item.device_name,item.date])
            });
            setUser(users);
        })
        .catch((err) => {
          console.error(err);
        });
    }
  };

  useEffect(() => {
    FetchClient();
  }, []);

  return (
    <Page fullWidth title="SMS Manager">
      <Stack vertical>
        <Stack.Item>
          <div
            onClick={() => (window.location.href = "/home")}
            style={{ cursor: "pointer" }}
          >
            <TextStyle variation="positive">
              <Heading>SMS Manager</Heading>
            </TextStyle>
          </div>
          <div onClick={() => logout()} style={{ cursor: "pointer" }}>
            <TextStyle variation="negative">
              <Heading>Logout</Heading>
            </TextStyle>
          </div>
        </Stack.Item>
      </Stack>

      <Card>
        <DataTable
          columnContentTypes={["text", "text", "text", "text"]}
          headings={["client_id", "android_version", "device_name", "date"]}
          rows={user}
        />
      </Card>
    </Page>
  );
};
export default Manager;
